body {
  background-color: #F2F2F2;
  font-family: "Lato", sans-serif;
  font-size: 15px; }

h1, h2, h3, h4, h5, h6 {
  color: #18814B;
  font-family: "Open Sans", sans-serif; }

img {
  max-width: 100%; }

a {
  transition: all 0.500s ease; }

blockquote {
  position: relative;
  margin-left: 0;
  margin-right: 0;
  padding-left: 1.5em;
  padding-right: 1.5em; }
  blockquote p {
    font-size: 1.3em; }
  blockquote::before {
    color: #6AABEE;
    content: "\f10d";
    font-family: "FontAwesome";
    position: absolute;
    left: 5px;
    top: 0; }
  blockquote::after {
    color: #6AABEE;
    content: "\f10e";
    font-family: "FontAwesome";
    position: absolute;
    right: 5px;
    bottom: 0; }

